import Head from 'next/head'

import { Container } from '../../components/layout/Container';
import { PostList } from '../../components/PostList';

import { IdentityProvider } from "../../hooks/identity";
import { ContextMenu } from "../../components/layout/ContextMenu";
import { EyeProvider, EyeOfJudgement } from '../../components/layout/EyeOfJudgement';
import { useState, useEffect } from 'react';

import { parse } from "querystring"
import { usePosts } from "../../hooks/posts"
import { Post } from '../../components/Post';

import s from "../../style/post.module.sass"

var array = [];
export default function Home() {
    var postObj = <p>ðŸ”„ loading ...</p>;


    var [parsed, setParsed] = useState(undefined);
    useEffect(()=> {
        console.log("setting parsed");
        var localParse = parse(location.search.substr(1))
        setParsed(localParse);
        if(localParse.id) array.push(localParse.id);
    }, []);

    var posts = usePosts(array);

    
    if(!parsed || !parsed.id) {
        if(parsed && !parsed.id)
            postObj = <p>âš  please specify ?id=POST_ID_HERE</p> 
    } else {
        if(posts[0]) {
            if(posts[0] === "ohjemine") postObj = <><h1>Error</h1><p style={{color: "rgb(255, 0, 143)"}}>Sowwy &gt;_&lt;</p></>;
            
            else postObj =<Post post={posts[0]} embed/>
        }
        if(posts[0] === null) postObj= <p>â›” post doesn't exist</p>;
    }
    
    return (
        <>
            <Head>
                <title>Civil Discussion</title>
            </Head>
            
            <IdentityProvider>
                <ContextMenu>
                    <div className={s.postEmbed}>
                        <div>
                            {postObj}
                        </div>
                        <a 
                            href="https://drkslv.github.io/discussion" 
                            target="_blank" 
                            style={{position: "absolute", bottom: "0px", right:"0px", display: "block", textDecoration:"none"}}
                        >
                            <EyeOfJudgement size={40} grow={false}/>
                        </a>
                    </div>
                    
                </ContextMenu>
            </IdentityProvider>
        </>
    )
}

function PostError() {
    return(
        <div className={s.card}></div>
    )
}

